.sidebar {
	width: 19.9%; }

.sidebar__title {
	padding: 7px 0 7px 14px;
	border-radius: 2px;
	background: #ae0002;
	color: #f6f6f6;
	font-family: $font-barlow;
	font-size: 18px;
	font-weight: 400;
	line-height: 22px;
	text-transform: uppercase; }

.sidebar__list {
	padding-top: 5px; }

.sidebar__list__item {
	position: relative; }

.sidebar__list__item__video {
	position: relative;
	margin-bottom: 12px; }


.sidebar__list__item__video--size {
	width: 100%; }

.sidebar__list__item__button {
	position: absolute;
	bottom: 7%;
	left: 4%;
	padding: 18px;
	border-radius: 2px;
	background: #262626;
	&:after {
		content: '';
		position: absolute;
		left: 30%;
		bottom: 23%;
		border-radius: 2px;
		border: 10px solid transparent;
		border-left: 18px solid #d2d2d2; } }

.sidebar__list__item__link {
	font-family: $font-georg;
	font-size: 12px;
	font-weight: 400;
	line-height: 18px;
	text-decoration: underline;
	color: #042b38;
	cursor: pointer; }

.sidebar__list__item__description {
	padding: 7px 0 25px;
	font-family: $font-georg;
	font-size: 11px;
	font-style: italic;
	line-height: 18px;
	color: #757575; }

.sidebar_list__item__coments {
	@extend %news_coments;
	position: absolute;
	bottom: 8.4%;
	left: 32%; }

.sidebar_list__item__coments__img {
	width: auto; }

.sidebar_list__item__coments__counter {
	@extend %news_counter;
	padding-left: 8px; }


//footer
.footer {
	max-width: 1000px;
	margin: 0 auto;
	padding: 21.6% 0 2%; }
.footer__info {
	font-family: $font-LucidaStd;
	font-size: 18px;
	font-weight: 700;
	line-height: 12.89px;
	text-transform: uppercase;
	color: #266781; }










