html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

body, html {
  min-width: 100%;
  min-height: 100%;
  line-height: 1;
  text-transform: none;
  text-rendering: auto;
  visibility: visible;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: none;
  -webkit-text-size-adjust: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block;
}

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img, fieldset, a img {
  border: none;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: #fff;
  text-decoration: none;
}

a:active, a:hover {
  outline: 0;
  text-decoration: none;
}

input[type="submit"], button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  background: 0;
  border: 0;
}

input, textarea {
  resize: none;
  user-select: text;
}

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  background-color: #fff !important;
  color: #000 !important;
}

input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  padding: 0;
}

code, kbd, pre, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

label {
  cursor: pointer;
}

.news_list__item__coments, .sidebar_list__item__coments {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.news_list__item__coments__counter, .basic_news__article__footer__counter, .sidebar_list__item__coments__counter {
  font-family: "Georgia";
  font-size: 11px;
  font-style: italic;
  line-height: 18px;
  color: #989898;
}

.top_news__header, .basic_news__header {
  display: flex;
  border-radius: 2px;
  background: #cacaca;
  align-items: center;
}

.top_news__header__title, .basic_news__header__title {
  position: relative;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #f6f6f6;
  padding: 7px 32px 7px 12px;
  text-transform: uppercase;
  background: #ae0002;
}

.top_news__header__title:before, .basic_news__header__title:before {
  content: '';
  position: absolute;
  left: 100%;
  bottom: 0;
  border: 18px solid transparent;
  border-left: 18px solid #ae0002;
}

.top_news__header__description, .basic_news__header__description {
  margin: 0 auto;
  font-family: "Georgia";
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  color: #757575;
}

@font-face {
  font-family: "LucidaStd";
  src: url(../fonts/LucidaStd-Bold.eot);
  src: url(../fonts/LucidaStd-Bold.woff);
  src: url(../fonts/LucidaStd-Bold.ttf);
  font-style: normal;
  font-weight: bold;
}

.wrap {
  margin: 0 auto;
  max-width: 1000px;
  background: #ededed;
}

.container {
  padding: 0 31px;
}

.body,
.header__main {
  background-size: cover;
  background-repeat: no-repeat;
}

.body {
  padding: 2.5% 0 10%;
  background: url("../img/bg/main-bg.png");
}

.header {
  background-image: linear-gradient(to top, #e2e2e2 0%, #ffffff 100%);
}

.header__main {
  background: url("../img/bg/bg-header.png");
  padding: 2.7% 0 2.3%;
  color: #f6f6f6;
  text-transform: uppercase;
}

.body,
.header__main {
  background-size: cover;
  background-repeat: no-repeat;
}

.header__hotest {
  position: relative;
  padding: 12px 0;
}

.header__hotest__label {
  font-family: "Barlow Condensed", sans-serif;
  background: #85afbd;
  padding: 1px 5px;
  border-radius: 3px;
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

.header__hotest__news {
  padding-left: 6px;
  font-family: "Georgia";
  font-size: 11px;
  font-style: italic;
  color: #757575;
}

.header__hotest__button {
  position: absolute;
  top: 22%;
  left: 68.4%;
  padding: 10px;
}

.header__hotest__button:after {
  content: '';
  display: block;
  width: 4px;
  height: 4px;
  border-right: 1px solid #757575;
  border-top: 1px solid #757575;
  transform: rotate(50deg);
}

.header__main__logo--layman {
  font-family: "LucidaStd";
  font-size: 30px;
}

.header__main__logo--politics {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 18px;
  letter-spacing: 12.24px;
  line-height: 22px;
}

.header__main__logo--politics:after {
  content: '';
  display: block;
  width: 94%;
  margin-top: 7px;
  width: 143px;
  height: 1px;
  background: #6892a0;
}

.header__main__logo--description {
  padding-top: 8px;
  font-family: "Lucida Sans Unicode";
  font-size: 10px;
  color: #6892a0;
  letter-spacing: 2px;
}

.header__main__logo,
.header__main__menu {
  display: inline-block;
  vertical-align: top;
}

.header__main__menu {
  padding-left: 1.8%;
}

.header__main__menu--bottom {
  padding-top: 2%;
}

.header__main__menu__link--top {
  color: #a9a9a9;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  padding-left: 11px;
}

.header__main__menu__link--top:hover {
  color: #ffffff;
}

.header__main__menu__link--bottom {
  padding: 6px 11px;
  font-family: "Barlow Condensed", sans-serif;
  color: #f6f6f6;
  font-size: 18px;
  line-height: 22px;
}

.header__main__menu__link--bottom:hover {
  background: #092f3b;
  box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1), inset 1px 1px 1px rgba(0, 0, 1, 0.28);
}

.news {
  display: flex;
  justify-content: space-between;
  padding: 3% 3.1% 2%;
}

.all_news {
  width: 77.2%;
}

.top_news__article {
  background: url("../img/bg/news-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 5px;
  padding: 15% 0 5% 5.5%;
}

.top_news__article__content {
  width: 43.5%;
  border-radius: 2px;
  background: #1e1e1e;
  padding: 2.5% 3.5% 3% 2.4%;
  font-family: "Georgia";
  text-align: justify;
  color: #e2e2e2;
  font-size: 12px;
  line-height: 18px;
}

.more_news {
  padding: 2% 0 6%;
}

.news_list {
  display: flex;
  justify-content: space-between;
}

.news_list__item {
  background: #ffffff;
  box-shadow: 1px 1px 1px rgba(0, 0, 1, 0.14);
  border-radius: 2px;
  width: 19%;
  padding: 17px 17px 0px;
}

.news_list__item__link {
  font-family: "Georgia";
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  text-decoration: underline;
  color: #042b38;
  cursor: pointer;
}

.news_list__item__img {
  position: relative;
  overflow: hidden;
  padding-bottom: 8.8%;
}

.news_list__item__img--size {
  max-width: 120%;
  width: 120%;
  margin-left: -15px;
  margin-top: -10px;
}

.news_list__item__description {
  padding-top: 5%;
  width: 91%;
  font-family: "Georgia";
  font-size: 11px;
  font-style: italic;
  line-height: 18px;
  color: #757575;
}

.news_list__item__coments {
  position: relative;
  bottom: 6px;
  left: 4px;
}

.news_list__item__coments__img {
  width: auto;
}

.news_list__item__coments__counter {
  padding-left: 6px;
}

.basic_news__article {
  display: flex;
  justify-content: space-between;
  padding: 26px 0 31px;
}

.basic_news__header__title--first {
  padding-right: 37px;
}

.basic_news__article__img {
  width: 23%;
}

.basic_news__article__content {
  width: 73%;
}

.basic_news__article__content__description,
.basic_news__article__footer__tags__link,
.basic_news__article__footer__tags__caption,
.basic_news__article__footer__social__share {
  font-family: "Georgia";
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.basic_news__article__footer {
  display: flex;
}

.basic_news__article__content__description {
  padding: 1.4% 0 3%;
  color: #757575;
}

.basic_news__article__footer__coments {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 7%;
}

.basic_news__article__footer__img {
  width: auto;
}

.basic_news__article__footer__counter {
  font-size: 13px;
}

.basic_news__article__footer__tags {
  padding: 0 31px 0 22px;
}

.basic_news__article__footer__tags__link {
  font-style: italic;
  text-decoration: underline;
  color: #2b5e5e;
}

.basic_news__article__footer__tags__link:after {
  display: inline-block;
  content: ',';
}

.basic_news__article__footer__tags__link:last-child:after {
  display: none;
}

.basic_news__article__footer__social {
  display: flex;
}

.basic_news__article__footer__tags__caption,
.basic_news__article__footer__social__share {
  color: #989898;
}

.basic_news__article__footer__social__share {
  padding-right: 15px;
}

.basic_news__article__footer__social__icon {
  margin-right: 7px;
}

.sidebar {
  width: 19.9%;
}

.sidebar__title {
  padding: 7px 0 7px 14px;
  border-radius: 2px;
  background: #ae0002;
  color: #f6f6f6;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-transform: uppercase;
}

.sidebar__list {
  padding-top: 5px;
}

.sidebar__list__item {
  position: relative;
}

.sidebar__list__item__video {
  position: relative;
  margin-bottom: 12px;
}

.sidebar__list__item__video--size {
  width: 100%;
}

.sidebar__list__item__button {
  position: absolute;
  bottom: 7%;
  left: 4%;
  padding: 18px;
  border-radius: 2px;
  background: #262626;
}

.sidebar__list__item__button:after {
  content: '';
  position: absolute;
  left: 30%;
  bottom: 23%;
  border-radius: 2px;
  border: 10px solid transparent;
  border-left: 18px solid #d2d2d2;
}

.sidebar__list__item__link {
  font-family: "Georgia";
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  text-decoration: underline;
  color: #042b38;
  cursor: pointer;
}

.sidebar__list__item__description {
  padding: 7px 0 25px;
  font-family: "Georgia";
  font-size: 11px;
  font-style: italic;
  line-height: 18px;
  color: #757575;
}

.sidebar_list__item__coments {
  position: absolute;
  bottom: 8.4%;
  left: 32%;
}

.sidebar_list__item__coments__img {
  width: auto;
}

.sidebar_list__item__coments__counter {
  padding-left: 8px;
}

.footer {
  max-width: 1000px;
  margin: 0 auto;
  padding: 21.6% 0 2%;
}

.footer__info {
  font-family: "LucidaStd";
  font-size: 18px;
  font-weight: 700;
  line-height: 12.89px;
  text-transform: uppercase;
  color: #266781;
}

@media screen and (max-width: 1010px) {
  .body {
    padding-top: 0;
  }
  .wrap {
    max-width: 1010px;
  }
}

@media screen and (max-width: 500px) {
  .news {
    padding: 3% 0% 2%;
  }
}

@media screen and (max-width: 905px) {
  .header__main__menu {
    padding-left: 0;
  }
  .header__main__menu__link--bottom {
    padding: 6px 8px;
    font-size: 15px;
  }
}

@media screen and (max-width: 770px) {
  .header__main__menu__link--bottom {
    padding: 6px 4px;
  }
}

@media screen and (max-width: 710px) {
  .header__hotest__button {
    position: static;
  }
  .header__main {
    position: relative;
    padding-bottom: 50px;
  }
  .header__main__menu--top {
    display: inline-block;
  }
  .header__main__menu__link--top {
    display: block;
    padding: 0 5px 7px;
  }
  .header__main__menu--bottom {
    position: absolute;
    bottom: 8%;
    left: 4%;
  }
}

@media screen and (max-width: 530px) {
  .header__main__menu__button {
    position: absolute;
    top: 15px;
    right: 5%;
    border-bottom: 9px double #a6a6a6;
    border-top: 3px solid #a6a6a6;
    height: 15px;
    width: 7%;
  }
  .header__main__menu__link--top {
    display: inline-block;
  }
  .header__main {
    padding-bottom: 15px;
  }
  .header__main__menu {
    display: block;
  }
  .header__main__menu__link--top {
    padding: 10px 10px 5px 0;
  }
  .header__main__menu--bottom {
    visibility: hidden;
    opacity: 0;
  }
  .header__main__menu--bottom__open {
    position: static;
    visibility: visible;
    opacity: 1;
    animation: menu-header-open 0.3s linear;
  }
  .header__main__menu__link--bottom {
    display: block;
    padding: 4px 0;
    color: white;
  }
}

@keyframes menu-header-open {
  0% {
    height: 0;
    opacity: 0;
  }
  100% {
    height: 200px;
    opacity: 1;
  }
}

@media screen and (max-width: 990px) {
  .top_news__header,
  .basic_news__header {
    flex-wrap: wrap;
  }
  .top_news__header__title,
  .basic_news__header__title {
    font-size: 15px;
  }
  .basic_news__header__title,
  .basic_news__header__title--first,
  .top_news__header__title {
    padding: 7px;
  }
}

@media screen and (max-width: 865px) {
  .top_news__header__title,
  .basic_news__header__title {
    width: 100%;
  }
  .top_news__header__title:before,
  .basic_news__header__title:before {
    display: none;
  }
  .top_news__header__description,
  .basic_news__header__description {
    width: 100%;
    padding: 7px;
  }
}

@media screen and (max-width: 400px) {
  .wrap {
    padding: 0;
  }
  .top_news__header__title,
  .basic_news__header__title,
  .sidebar__title {
    font-size: 15px;
  }
}

@media screen and (max-width: 400px) {
  .top_news__article {
    padding: 25% 0 15% 3%;
  }
  .top_news__article__content {
    width: 70%;
  }
}

@media screen and (max-width: 930px) {
  .news_list__item__link {
    font-size: 11px;
  }
  .news_list__item__description {
    width: 100%;
  }
  .news_list__item__coments {
    position: static;
    padding-bottom: 10px;
  }
}

@media screen and (max-width: 860px) {
  .more_news {
    padding-bottom: 2%;
  }
  .news_list {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .news_list__item {
    width: 26%;
    min-width: 150px;
    margin-bottom: 10px;
  }
  .news_list__item__img--size {
    max-width: 100%;
    width: 100%;
    margin: 0;
  }
}

@media screen and (max-width: 780px) {
  .news_list {
    justify-content: space-between;
  }
  .news_list__item {
    width: 40%;
  }
  .news_list__item__link {
    font-size: 15px;
  }
}

@media screen and (max-width: 400px) {
  .news_list__item {
    width: 100%;
    text-align: center;
  }
  .news_list__item__img {
    width: 75%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 350px) {
  .news_list__item {
    width: 100%;
  }
}

@media screen and (max-width: 320px) {
  .news_list__item__img {
    width: 100%;
  }
}

@media screen and (max-width: 940px) {
  .basic_news__article {
    position: relative;
    padding-bottom: 60px;
  }
  .basic_news__article__footer {
    position: absolute;
    flex-wrap: wrap;
    bottom: 10%;
    left: 0;
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .basic_news__article__img {
    width: 43%;
  }
  .basic_news__article__content {
    width: 55%;
  }
}

@media screen and (max-width: 675px) {
  .basic_news__article__footer__tags {
    padding: 0 15px;
  }
  .basic_news__article__footer__social__share {
    padding-right: 5px;
  }
  .basic_news__article__content__description,
  .basic_news__article__footer__tags__link,
  .basic_news__article__footer__tags__caption,
  .basic_news__article__footer__social__share {
    font-size: 11px;
  }
}

@media screen and (max-width: 500px) {
  .basic_news__article {
    padding-left: 10px;
    padding-right: 10px;
  }
  .basic_news__article__footer {
    left: 1.5%;
  }
}

@media screen and (max-width: 470px) {
  .basic_news__article {
    flex-wrap: wrap;
    padding-bottom: 15px;
  }
  .basic_news__article__img {
    width: 50%;
    margin: 0 auto;
  }
  .basic_news__article__content {
    width: 100%;
    text-align: justify;
  }
  .basic_news__article__footer__coments,
  .basic_news__article__footer__tags,
  .basic_news__article__footer__social {
    padding-top: 10px;
  }
  .basic_news__article__footer {
    position: static;
  }
}

@media screen and (max-width: 353px) {
  .basic_news__article__img {
    width: 75%;
  }
  .basic_news__article__footer__tags {
    width: 100%;
    padding: 0;
  }
}

@media screen and (max-width: 300px) {
  .basic_news__article__img {
    width: 90%;
  }
}

@media screen and (max-width: 930px) {
  .sidebar_list__item__coments {
    position: static;
    padding-bottom: 15px;
  }
  .sidebar__list__item__description {
    padding-bottom: 0;
  }
}

@media screen and (max-width: 600px) {
  .news {
    flex-wrap: wrap;
  }
  .all_news,
  .sidebar {
    width: 100%;
  }
  .sidebar__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .sidebar__list__item {
    width: 30%;
    min-width: 120px;
  }
}

@media screen and (max-width: 435px) {
  .sidebar__list__item__link {
    font-size: 11px;
  }
}

@media screen and (max-width: 400px) {
  .sidebar__list__item {
    width: 75%;
  }
  .sidebar__list__item__link {
    font-size: 15px;
  }
}

@media screen and (max-width: 300px) {
  .sidebar__list__item {
    width: 100%;
  }
}

@media screen and (max-width: 1010px) {
  .footer {
    padding: 10% 3% 2%;
  }
}
