$font-barlow: 'Barlow Condensed', sans-serif;
$font-lucidaSans: 'Lucida Sans Unicode';
$font-LucidaStd: 'LucidaStd';
$font-georg: 'Georgia';

%news_coments {
	display: flex;
	justify-content: flex-end;
	align-items: flex-end; }

%news_counter {
	font-family: $font-georg;
	font-size: 11px;
	font-style: italic;
	line-height: 18px;
	color: #989898; }

%news__header {
	display: flex;
	border-radius: 2px;
	background: #cacaca;
	align-items: center; }

%news__header__title {
	position: relative;
	border-top-left-radius: 2px;
	border-bottom-left-radius: 2px;
	font-family: $font-barlow;
	font-size: 18px;
	font-weight: 400;
	line-height: 22px;
	color: #f6f6f6;
	padding: 7px 32px 7px 12px;
	text-transform: uppercase;
	background: #ae0002;
	&:before {
		content: '';
		position: absolute;
		left: 100%;
		bottom: 0;
		border: 18px solid transparent;
		border-left: 18px solid #ae0002; } }

%news__description {
	margin: 0 auto;
	font-family: $font-georg;
	font-size: 12px;
	font-weight: 400;
	line-height: 18px;
	color: #757575; }
